.travel {
  background: url(/assets/src/img/travel/decor/rectangle.png) no-repeat 0 70%;
}

.travel__body {
  display: flex;
  padding: 64px 0;
  gap: 33px;
}

.travel__img {
  height: 697px;
  width: 697px;
  flex-shrink: 0;
  margin-left: -72px;
  position: relative;
}

.travel__content {
  position: relative;
}

.travel__marker {
  right: 0;
  top: 131px;
  position: absolute;

  svg {
    fill: #FF5722;
  }

  span {
    font-size: 23px;
  }
}

.travel__titleWrapper {
  margin: 14px 0 32px;
}

.travel__text {
  margin-bottom: 64px;
  max-width: 445px;
}

.travel__decor {
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  right: -81px;
  top: 293px;
  height: 112px;
  width: 112px;
  border-radius: 50%;
  background: #FACD49;
  box-shadow: 0 15px 34px 0 rgba(0, 0, 0, 0.03),
              0 62px 62px 0 rgba(0, 0, 0, 0.02),
              0 139px 83px 0 rgba(0, 0, 0, 0.01),
              0 246px 99px 0 rgba(0, 0, 0, 0.00);

  svg {
    fill: #fff;
  }
}

.travel__items {
  display: grid;
  grid-template: repeat(2, 153px) / repeat(2, 211px);
  gap: 30px;
}

.travel__item {
  text-align: center;
  padding: 32px;
  border-radius: 32px;
  border: 1px solid rgba(25, 24, 37, 0.10);
  background: #fff;
}

.travel__itemTitle {
  font-family: Circularstd, sans-serif;
  color: #FF5722;
  font-size: 35px;
  font-weight: 700;
  line-height: 1.2;
  margin-bottom: 16px;
}

.travel__itemText {
  color: #191825;
}