.subscribe {
  position: relative;

  &::before {
    content: url(/assets/src/img/subscribe/decor/decor-2.png);
    display: block;
    position: absolute;
    bottom: -110px;
    right: 0;
  }
}

.subscribe__body {
  padding: 64px 0;
  position: relative;

  &::before {
    content: url(/assets/src/img/subscribe/decor/decor-1.svg);
    display: block;
    position: absolute;
    top: 5px;
    left: -43px;
  }
}

.subscribe__form {
  padding: 128px 64px;
  border-radius: 32px;
  background: rgba(250, 205, 73, 0.08);
}

.subscribe__titleWrapper {
  text-align: center;
  margin-bottom: 64px;

  .titleWrapper__subtitle {
    margin-bottom: 32px;
  }

  .titleWrapper__title {
    font-size: 55px;
  }
}

.subscribe__content {
  display: flex;
  align-items: center;
  gap: 64px;
}

.subscribe__inputMail {
  padding: 0 32px;
  display: flex;
  align-items: center;
  flex-grow: 1;
  border-radius: 32px;
  background: #fff;

  &::before {
    content: url(/assets/src/img/subscribe/message.svg);
    display: block;
    margin-right: 16px;
    height: 32px;
    width: 32px;
  }

  input {
    color: rgba(25, 24, 37, 0.75);
    border: none;
    font-family: Circularstd, sans-serif;
    font-size: 23px;
    font-weight: 700;
    line-height: 1.2;
    padding: 32px 0;
    width: 100%;
  }
}

.subscribe__btn {
  color: #fff;
  padding: 32px 64px;
  font-size: 23px;
}
