.welcome {

  .blockMarker {
    font-size: 14px;
  }
}

.welcome__body {
  display: flex;
  gap: 90px;
  padding: 260px 0 70px;
  position: relative;

  &::before {
    content: '';
    display: block;
    position: absolute;
    width: 500px;
    height: 500px;
    border-radius: 50%;
    opacity: 0.2;
    background: #FF5722;
    filter: blur(250px);
    top: -193px;
    left: -309px;
    z-index: -1;
  }
}

.welcome__marker1 {
  color: #F85E9F;
  margin-bottom: 43px;
  box-shadow: 0 0 0 0 rgba(0, 0, 0, 0.07),
  0 34px 75px 0 rgba(0, 0, 0, 0.07),
  0 137px 137px 0 rgba(0, 0, 0, 0.06),
  0 308px 185px 0 rgba(0, 0, 0, 0.04),
  0 548px 219px 0 rgba(0, 0, 0, 0.01),
  0 856px 240px 0 rgba(0, 0, 0, 0.00);

  svg {
    order: 1;
    fill: #F85E9F;
  }
}

.welcome__marker2 {
  color: #222831;
  border: 1px solid #EEE;
  column-gap: 8px;

  svg {
    fill: #5D50C6;
  }
}

.welcome__contentWrapper {
  max-width: 427px;
}

h1.welcome__title {
  color: #000;
  font-family: Circularstd, sans-serif;
  font-size: 69px;
  font-weight: 700;
  line-height: 1.2;
  margin-bottom: 43px;

  span {
    color: #F85E9F;
  }
}

.welcome__text {
  margin-bottom: 43px;
}

.welcome__btns {
  display: flex;
  align-items: center;
  gap: 16px;
}

.welcome__startBtn {
  box-shadow: 0 0 0 0 rgba(0, 0, 0, 0.10),
  0 5px 11px 0 rgba(0, 0, 0, 0.10),
  0 20px 20px 0 rgba(0, 0, 0, 0.09),
  0 45px 27px 0 rgba(0, 0, 0, 0.05),
  0 81px 32px 0 rgba(0, 0, 0, 0.01),
  0 126px 35px 0 rgba(0, 0, 0, 0.00);
}

.welcome__imgsWrapper {
  position: relative;
}

.welcome__imgsWrapperBg {
  background: url(/assets/src/img/welcome/earth.png) no-repeat center/cover;
  position: absolute;
  height: 288px;
  width: 772px;
  left: -91px;
  top: -75px;
  z-index: 0;
}

.welcome__imgsWrapperBody {
  position: relative;
  z-index: 1;
  display: grid;
  column-gap: 30px;
  grid-template-columns: repeat(2, 272px);
  grid-template-rows: repeat(630, 1px);


  img {
    border-radius: 32px;

    &:nth-of-type(1) {
      grid-row-start: 1;
      grid-row-end: 301;
    }

    &:nth-of-type(2) {
      grid-row-start: 116;
      grid-row-end: 516;
    }

    &:nth-of-type(3) {
      grid-row-start: 331;
      grid-row-end: 631;
    }
  }

  :nth-child(1 of .welcome__decor) {
    background-color: #F85E9F;
    left: -32px;
    top: 260px;
  }

  :nth-child(2 of .welcome__decor) {
    background-color: #FF5722;
    bottom: -5px;
    right: 130px;
  }
}

.welcome__decor {
  position: absolute;
  width: 64px;
  height: 64px;
  border-radius: 50%;
  box-shadow: 0 19px 19px 0 rgba(0, 0, 0, 0.09),
  0 43px 26px 0 rgba(0, 0, 0, 0.05),
  0 77px 31px 0 rgba(0, 0, 0, 0.01),
  0 120px 34px 0 rgba(0, 0, 0, 0.00);
  display: flex;
  align-items: center;
  justify-content: center;

  svg {
    fill: #fff;
  }
}

.welcome__marker3 {
  position: absolute;
  right: -92px;
  top: 402px;
  box-shadow: 0 43px 26px 0 rgba(0, 0, 0, 0.05),
  0 77px 31px 0 rgba(0, 0, 0, 0.01),
  0 120px 34px 0 rgba(0, 0, 0, 0.00);

  svg {
    fill: #FACD49;
  }
}





