.banners__container {
  max-width: 2000px;
  overflow: hidden;
  margin: -300px auto;
  padding: 300px 0;
}

.banners__body {
  padding: 64px 0;
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-wrap: wrap;
  position: relative;

  &:before {
    content: url(/assets/src/img/banners/decor.svg);
    display: block;
    position: absolute;
    left: -120px;
    top: -50px;
  }

  &::after {
    content: '';
    display: block;
    position: absolute;
    width: 500px;
    height: 500px;
    border-radius: 50%;
    opacity: 0.2;
    background: #FACD49;
    filter: blur(250px);
    top: -100px;
    right: -310px;
    z-index: -1;
  }
}

