.burgerBtn {
  border: none;
  background-color: #fff;
  display: none;
  width: 28px;
  height: 28px;
  position: relative;
  z-index: var(--ziBurgerBtn);

  span {
    position: absolute;
    left: 0;
    right: 0;
    display: block;
    outline: 1px solid #000;
    height: 0;
    transition: outline 0.3s 0.3s, transform 0.3s, top 0.3s 0.3s, bottom 0.3s 0.3s;

    &:first-child {
      top: 6px;
    }

    &:nth-child(2) {
      top: 50%;
      transform: translateY(-50%);
    }

    &:last-child {
      top: 22px;
    }
  }

  &.active {

    span {

      &:first-child,
      &:last-child {
        top: 50%;
        transition: top 0.3s, transform 0.3s 0.3s;
      }

      &:first-child {
        transform: translateY(-50%) rotate(45deg);
      }

      &:nth-child(2) {
        outline-width: 0;
        transition: outline 0.3s;
      }

      &:last-child {
        transform: translateY(-50%) rotate(-45deg);
      }
    }

  }
}


.burgerContainer {
  background-color: rgba(0,0,0,0.2);
  position: absolute;
  inset: 0;
  transform: translateX(-100%);
  transition: transform 0.3s 0.3s;
  overflow: hidden;
  z-index: 2;

  nav {
    background-color: #fff;
    padding: 30px;
    width: max-content;
    position: absolute;
    right: 0;
    top: 130px;
    transform: translateX(100%);
    transition: transform 0.3s;

    ul {
      margin: -20px 0;
    }

    li {
      margin: 20px 0;
    }

    a {
      color: inherit;
      transition: opacity 0.3s;

      &:hover,
      &:focus {
        opacity: 0.5;
      }
    }
  }

  &.active {
    transform: translateX(0);
    transition: transform 0.3s;

    nav {
      display: block;
      transform: translateX(0);
      transition: transform 0.3s 0.3s;
    }
  }
}


@mixin burger-992 {

  .burgerBtn {
    display: block;
  }

}

@mixin burger-576 {

  .burgerBtn {
    height: 20px;
    width: 20px;

    span {

      &:first-child {
        top: 3px;
      }

      &:last-child {
        top: 17px;
      }
    }

  }

  .burgerContainer {

    nav {
      top: 23vw;
    }
  }

}