.sliderBtn {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100px;
  width: 100px;
  border: 1px solid rgba(25, 24, 37, 0.10);
  border-radius: 50%;
  background-color: #fff;
  transition: background-color 0.3s;

  svg {
    fill: #191825;
    transition: fill 0.3s;
  }

  &:hover,
  &:focus-visible {
    background-color: #5D50C6;

    svg {
      fill: #fff;
    }
  }
}

.sliderBtn--next {
  transform: rotate(180deg);
}