.destination {

  .slick-slide {
    padding: 0 15px;
  }
}

.destination__body {
  padding: 64px 0;
  position: relative;

  &:before {
    content: url(/assets/src/img/destination/decor.svg);
    display: block;
    position: absolute;
    right: -130px;
    top: 200px;
  }
}

.destination__bodyHeader {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 30px;
  flex-wrap: wrap;
  margin-bottom: 64px;
}

.destination__sliderBtns {
  display: flex;
  gap: 32px;
}

.destination__slider {
  margin: 0 -15px;
  position: relative;


  &::before {
    content: '';
    display: block;
    position: absolute;
    inset: auto 15px 0 15px;
    height: 225px;
    border-radius: 32px;
    background: linear-gradient(to bottom, rgba(0, 0, 0, 0.04), rgba(0, 0, 0, 0.09));
    box-shadow:
      0 32px 71px 70px rgba(0, 0, 0, 0.05),
      0 128px 128px 0 rgba(0, 0, 0, 0.04),
      0 288px 173px 0 rgba(0, 0, 0, 0.03),
      0 513px 205px 0 rgba(0, 0, 0, 0.01);
  }
}

.destination__sliderItem {
  border-radius: 32px;
  overflow: hidden;
}

.destination__sliderItemImg {
  height: 350px;
}

.destination__sliderItemContent {
  background-color: #fff;
  padding: 32px;
  font-family: Circularstd, sans-serif;
  font-weight: 700;
  font-size: 23px;
  line-height: 1.2;
}

.destination__sliderItemWrapper {
  display: flex;
  row-gap: 30px;
  margin-bottom: 16px;
}

.destination__sliderItemCost {
  color: #F85E9F;
}

.destination__sliderItemText {
  margin-bottom: 32px;
  line-height: 1.6;
}

.destination__sliderItemRating {
  display: flex;
  align-items: center;
  column-gap: 8px;
  color: #FF5722;

  svg {
    fill: #FF5722;
  }
}