.header {
  font-family: Circularstd, sans-serif;
  font-size: 14px;
  font-weight: 700;
  line-height: 1.2;
  position: absolute;
  width: 100%;
  z-index: var(--ziHeader);
}

.header__body {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 32px 0;
}

.header__loginBtn {
  background-color: transparent;
  color: #222831;
}

.header__menu {
  flex-grow: 1;

  ul {
    color: rgba(25, 24, 37, 0.50);
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 15px 64px;
  }

  a {
    transition: color 0.3s;

    &:hover,
    &:focus-visible {
      color: #222831;
    }
  }
}
