@import "../fonts.scss";

*,
*::before,
*::after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

.wrapper {
  color: #191825;
  display: flex;
  flex-direction: column;
  min-height: 100vh;

  a,
  input,
  textarea {
    color: inherit;
  }

  button {
    cursor: pointer;
  }

  a:not(.outline),
  input:not(.outline),
  textarea:not(.outline),
  button:not(.outline) {
    outline: none;
  }

  a:not(.textDecoration) {
    text-decoration: none;
  }

  ul:not(.listStyle) {
    list-style: none;
  }

  img {
    object-fit: cover;
    max-width: 100%;
  }
}

.container {
  margin: 0 auto;
  max-width: 1150px;
  padding: 0 20px;
}

.main {
  flex-grow: 1;
}