.services__container {
  max-width: 2000px;
  margin: 0 auto -100px;
  padding-bottom: 100px;
  overflow: hidden;
}

.services__body {
  display: flex;
  align-items: center;
  padding: 64px 0;
}

.services__titleWrapper {
  max-width: 444px;
  flex-shrink: 0;
}

.services__cards {
  display: flex;
  gap: 21px;
}

.services__card {
  border-radius: 32px;
  background: #FFF;
  width: 350px;
  padding: 64px;
  text-align: center;

  &:nth-child(2) {
    box-shadow: 0 0 0 0 rgba(0, 0, 0, 0.10),
    0 41px 89px 0 rgba(0, 0, 0, 0.10);
  }
}

.services__cardImg {
  height: 64px;
  width: 64px;
  margin: 0 auto 64px auto;
}

.services__cardTitle {
  font-family: Circularstd, sans-serif;
  font-size: 28px;
  font-weight: 700;
  line-height: 1.2;
  margin-bottom: 32px;
}