.blockBtn {
  align-items: center;
  border: none;
  display: inline-flex;
  font-family: Circularstd, sans-serif;
  font-weight: 700;
  line-height: 1.2;
  justify-content: center;
  text-align: center;
  transition: transform 0.3s;

  &:hover,
  &:focus-visible {
    transform: scale(1.1);
  }
}

.blockBtn--1 {
  color: #EEE;
  font-size: 14px;
  padding: 16px 32px;
  border-radius: 100px;
  background-color: #5D50C6;
}


