.socials {

  ul {
    display: flex;
    column-gap: 32px;
  }

  li {

  }

  a {
    display: inline-block;

    &:hover,
    &:focus {

      svg {
        fill: #9a94c7;

      }
    }
  }

  svg {
    height: 32px;
    fill: #5D50C6;
    transition: fill 0.3s;
    width: 32px;
  }
}