.features__body {
  display: flex;
  gap: 42px;
  padding: 64px 0;
  position: relative;

  &::before {
    content: url(/assets/src/img/features/decor.png);
    position: absolute;
    display: block;
    top: 47px;
    right: -54px;
  }

}

.features__content {
  flex-shrink: 0;
  max-width: 540px;
}

.features__titleWrapper {
  margin-bottom: 32px;
}

.features__text {
  margin-bottom: 64px;
}

.features__item {
  display: flex;
  align-items: center;
  padding: 32px;
  background: #fff;

  &:nth-child(1) {

    .features__itemImg {
      background-color: #FF5722;
    }
  }

  &:nth-child(2) {
    border-radius: 32px;
    border: 1px solid rgba(25, 24, 37, 0.10);

    .features__itemImg {
      background-color: #FACD49;
    }
  }

  &:nth-child(3) {

    .features__itemImg {
      background-color: #F85E9F;
    }
  }
}

.features__itemImg {
  margin-right: 32px;
  height: 100px;
  width: 100px;
  border-radius: 32px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.features__itemTitle {
  margin-bottom: 8px;
  font-family: Circularstd,sans-serif;
  font-size: 23px;
  font-weight: 700;
  line-height: 1.2;
}

.features__imgs {
  position: relative;
  flex-grow: 1;
}

.features__img {
  border-radius: 500px;
  overflow: hidden;
}

.features__img--first {
  height: 665px;
  width: 443px;
  margin-top: 56px;
}

.features__img--second {
  border: 24px solid #FFF;
  width: 399px;
  height: 544px;
  position: absolute;
  left: 161px;
  top: 308px;
}

.features__marker {
  box-shadow: 0 15px 34px 0 rgba(0, 0, 0, 0.03),
              0 62px 62px 0 rgba(0, 0, 0, 0.02),
              0 139px 83px 0 rgba(0, 0, 0, 0.01),
              0 246px 99px 0 rgba(0, 0, 0, 0.00);
  position: absolute;
  top: 143px;
  right: -8px;
  font-size: 23px;

  svg {
    fill: #FF5722;
  }
}