.footer__body {
  padding: 64px 0;
}

.footer__wrapper {
  display: flex;
  gap: 60px 153px;
}

.footer__content {
  max-width: 278px;
}

.footer__logo {
  margin-bottom: 32px;
}

.footer__text {
  color: rgba(25, 24, 37, 0.50);
  margin-bottom: 64px;
}

.footer__nav {
  display: flex;
  gap: 60px 121px;
}

.footer__navColumn {
  flex-shrink: 0;
}

.footer__navTitle {
  font-family: Circularstd, sans-serif;
  font-size: 23px;
  font-weight: 700;
  line-height: 1.2;
  margin-bottom: 32px;
}

.footer__navList {

  li {
    margin-bottom: 32px;
    color: rgba(25, 24, 37, 0.75);

    &:last-child {
      margin-bottom: 0;
    }

    .blockText {
      color: inherit;
    }
  }

  a {
    transition: color 0.3s;

    &:hover,
    &:focus-visible {
      color: #000;
    }
  }
}
