.testimonials {
  background: url(/assets/src/img/testimonials/bg.png) no-repeat center/cover;
  padding: 300px 0;
  margin: -300px 0;
}

.testimonials__body {
  padding: 64px 0;
}

.testimonials__titleWrapper {
  text-align: center;
  margin-bottom: 64px;
}

.testimonials__slider {
  position: relative;
}

.testimonials__sliderBtn {
  position: absolute;
  top: 100px;
  z-index: 1;
}

.testimonials__sliderBtn--prev {
  left: 0;
}

.testimonials__sliderBtn--next {
  right: 0;
}

.testimonials__sliderBody {
  position: relative;
  z-index: 0;
}

.testimonials__sliderItem {
  font-family: Circularstd, sans-serif;
}

.testimonials__sliderItemBody {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 0 100px;
}

.testimonials__sliderItemImg {
  width: 128px;
  height: 128px;
  border-radius: 50%;
  overflow: hidden;
  margin-bottom: 64px;
}

.testimonials__sliderItemTitle {
  margin-bottom: 32px;
}

.testimonials__sliderItemName {
  color: #FF5722;
  font-size: 28px;
  font-weight: 700;
  line-height: 1.2;
}

.testimonials__sliderItemSeparate,
.testimonials__sliderItemProf {
  color: rgba(25, 24, 37, 0.75);
  font-size: 23px;
  font-weight: 700;
  line-height: 1.2;
}

.testimonials__sliderItemRating {
  display: flex;
  align-items: center;
  column-gap: 16px;
  margin-bottom: 32px;

  svg {
    height: 32px;
    width: 32px;
  }
}

.testimonials__sliderItemRating--disable {
  fill: rgba(0,0,0,0.05);
}

.testimonials__sliderItemRating--enable {
  fill: #FACD49;
}

.testimonials__sliderItemText {
  text-align: center;
}

.testimonials__sliderDots {
  height: 24px;
  margin-top: 64px;

  li {
    height: 24px;
    width: 24px;
    margin: 0 12px;

    button {
      background-color: rgba(25, 24, 37, 0.05);
      border-radius: 50%;
      height: 100%;
      width: 100%;

      &::before {
        display: none;
      }
    }

    &.slick-active {

      button {
        background-color: #F85E9F;
      }
    }
  }

}